import React from 'react'
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react'
import { PropTypes } from 'prop-types'

function FooterMap(props){
    const mapStyles = {
        width: '100%',
        height: '100%'
    };

    return (
        <Map
            google={props.google}
            zoom={10}
            style={mapStyles}
            
            initialCenter={{ lat: props.latitude, lng: props.longitude}}
        >
            <Marker 
                position={{ lat: props.latitude, lng: props.longitude}} 
                icon={{
                    url: process.env.PUBLIC_URL+'/img/icon-img/2.png'
                }}
                animation = {props.google.maps.Animation.BOUNCE}
            />
        </Map>
    );
}

FooterMap.propTypes = {
    latitude: PropTypes.string,
    longitude: PropTypes.string
}

export default GoogleApiWrapper({
    apiKey: "AIzaSyCGM-62ap9R-huo50hJDn05j3x-mU9151Y"
})(FooterMap);