import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { IntlActions } from 'react-redux-multilingual'

import HeaderSearch from './components/HeaderSearch'
import NavbarIcons from './components/NavbarIcons'
import HeaderMenuAside from './components/HeaderMenuAside'
import HeaderCartAside from './components/HeaderCartAside'
import MobileMenu from './components/MobileMenu'
import HeaderNavMenu from './components/HeaderNavMenu'
import store from '../../../store'
import { changeCurrency } from '../../../actions'

class HeaderOne extends Component {

    constructor(props){
        super(props);
        this.state = {};
        this.handleScroll = this.handleScroll.bind(this);
    }

    
    changeLanguage(lang) {
        store.dispatch(IntlActions.setLocale(lang))
	}

    handleScroll() {
        if(this.mount){
            this.setState({scroll: window.scrollY});
        }
    }
    
    componentDidMount() {
        this.mount = true;
        const el = document.querySelector('header');
        this.setState({top: el.offsetTop, height: el.offsetHeight});
        window.addEventListener('scroll', this.handleScroll);
    }
    
    componentDidUpdate() {
        this.state.scroll > this.state.top ? 
        document.body.style.paddingTop = `${this.state.height}px` :
        document.body.style.paddingTop = 0;
    }

    componentWillUnmount(){
        this.mount = false;
    }

    render() {
        return (
            <div>
                <header className= {`header-area header-padding-1 section-padding-1 sticky-bar ${this.props.type === "transparent" ? 'transparent-bar' : ''} ${this.state.scroll > this.state.top ? "stick" : ""}`}>
                    <div className={`header-top bg-gray-2 d-none ${this.props.topBar === 'hidden' ? 'd-lg-none': 'd-lg-block'}`}>
                        <div className="container-fluid">
                            <div className="row align-items-center">
                            <div className="col-lg-8">
                                <div className="header-contact-info">
                                <ul className="d-flex">
                                    <li><i className="fa fa-phone"/> <a href="tel:123456789">+56.653.534</a></li>
                                    <li><i className="fa fa-envelope" /> <a href="mailto:info@domain.com">INFO@DOMAIN.COM</a></li>
                                    <li><i className="fa fa-clock-o" /> MON-SAT:8AM TO 9PM</li>
                                </ul>
                                </div>
                            </div>
                            <div className="col-lg-4">
                                <div className="header-top-cur-lang">
                                <ul>
                                    <li><button>CURRENCY </button>
                                        <ul>
                                            <li><button onClick={() => this.props.changeCurrency('$')}>USD</button></li>
                                            <li><button onClick={() => this.props.changeCurrency('')}>EUR</button></li>
                                            <li><button onClick={() => this.props.changeCurrency('')}>GBP</button></li>
                                        </ul>
                                    </li>
                                    <li><button>LANGUAGE</button>
                                        <ul>
                                            <li><button onClick={() => this.changeLanguage('en')}>English</button></li>
                                            <li><button onClick={() => this.changeLanguage('de')}>German</button></li>
                                            <li><button onClick={() => this.changeLanguage('fn')}>French</button></li>
                                        </ul>
                                    </li>
                                </ul>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>

                    <div className="container-fluid">
                        <div className="row align-items-center">
                            <div className="col-xl-2 col-lg-2 col-md-6 col-4">
                                <div className="logo">
                                    <Link to={process.env.PUBLIC_URL+'/'}>
                                        <img src={process.env.PUBLIC_URL + this.props.logo} alt="" />
                                    </Link>
                                </div>
                            </div>
                            <div className="col-xl-8 col-lg-8 d-none d-lg-block">
                                <div className="main-menu main-menu-center">
                                    {/* header nav menu */}
                                    <HeaderNavMenu />
                                </div>
                            </div>
                            <div className="col-xl-2 col-lg-2 col-md-6 col-8">
                                {/* navbar icon */}
                                <NavbarIcons />
                            </div>
                        </div>
                    </div>
                </header>

                {/* header search */}
                <HeaderSearch />
                
                {/* header menu aside */}
                <HeaderMenuAside />
                
                {/* header cart aside */}
                <HeaderCartAside />

                {/* mobile menu */}
                <MobileMenu />

            </div>
        )
    }
}

export default connect(null, {changeCurrency})(HeaderOne)