// cart total price
export const getCartTotalPrice = cartProducts => {
    var totalAmount = 0;
    for(let i=0; i < cartProducts.length; i++){
        totalAmount += parseFloat(cartProducts[i].sum);
    }
    return totalAmount;
}

// get products
export const getProducts = (products, category, count, type) => {

    const items = products.filter(product => {

            //get new products
            if(type === "new"){
                return product.new === true && ( category ? product.category.indexOf(category) !== -1 : true ) ;
            }

            //get sale products
            if(type === "sale"){
                return product.discount > 0 && ( category ? product.category.indexOf(category) !== -1 : true ) ;
            }

            //get rated products
            if(type === "rating"){
                return product.rating >= 4 && ( category ? product.category.indexOf(category) !== -1 : true ) ;
            }
            
            // get products
            return ( category ? product.category.indexOf(category) !== -1 : true ) ;
     
    })

    return items.slice(0,count)
}

// calculate discount
export const calculateDiscount = (mainPrice, discount) => {
    mainPrice = Number(mainPrice);
    discount = Number(discount) / 100;
    let discountedPrice = mainPrice - ( mainPrice * discount );
    return discountedPrice;
}

// get unique categories
export const getCategories = (products) => {
    let uniqueCategories = [];
    products.map((product)=>{
        if(product.category){
            product.category.map((category)=>{
                if(uniqueCategories.indexOf(category) === -1){
                    uniqueCategories.push(category);
                }
                return uniqueCategories;
            })
        }
        return product;
    })
    return uniqueCategories;
}

