// get maximum and minimum price
export const getMaxMinPrice = (products) => {
    let min = 10, max = 1000;

    products.map((product) => {
        let v = product.price;
        min = (v < min) ? v : min;
        max = (v > max) ? v : max;
        return product;
    })

    return {'min':min, 'max':max};
}

export const getShopProducts = (products, { category, color, size, tag, value, sortBy }) => {

    return products.filter(product => {
        let categoryFilter;
        if(category && product.category) {
            categoryFilter = product.category.includes(category)
        }else{
            categoryFilter = true;
        }

        let colorFilter;
        if(color && product.colors) {
            colorFilter = product.colors.includes(color)
        }else{
            colorFilter = true;
        }

        let sizeFilter;
        if(size && product.sizes) {
            sizeFilter = product.sizes.includes(size)
        }else{
            sizeFilter = true;
        }

        let tagFilter;
        if(tag && product.tags) {
            tagFilter = product.tags.includes(tag)
        }else{
            tagFilter = true;
        }

        const startPriceMatch = typeof value.min !== 'number' || value.min <= product.price;
        const endPriceMatch = typeof value.max !== 'number' || product.price <= value.max;

        return categoryFilter && colorFilter && sizeFilter && tagFilter && startPriceMatch && endPriceMatch;
    }).sort((product1, product2) => {
        if (sortBy === 'priceHighToLow') {
            return product2.price < product1.price ? -1 : 1;
        } else if (sortBy === 'priceLowToHigh') {
            return product2.price > product1.price ? -1 : 1;
        } else if (sortBy === 'nameAscOrder') {
            return product1.name.localeCompare(product2.name);
        } else if (sortBy === 'nameDescOrder') {
            return product2.name.localeCompare(product1.name);
        } else{
            return product2.id > product1.id ? -1 : 1;
        }
    });
}