import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import HeaderSearch from './components/HeaderSearch'
import NavbarIconsTwo from './components/NavbarIconsTwo'
import HeaderNavigationAside from './components/HeaderNavigationAside'
import HeaderCartAside from './components/HeaderCartAside'
import MobileMenu from './components/MobileMenu'

class HeaderTwo extends Component {

    constructor(props){
        super(props);
        this.state = {};
        this.handleScroll = this.handleScroll.bind(this);
    }

    handleScroll() {
        if(this.mount){
            this.setState({scroll: window.scrollY});
        }
    }

    componentDidMount() {
        this.mount = true;
        const el = document.querySelector('header');
        this.setState({top: el.offsetTop, height: el.offsetHeight});
        window.addEventListener('scroll', this.handleScroll);
    }
 
    componentDidUpdate() {
        this.state.scroll > this.state.top ? 
        document.body.style.paddingTop = `${this.state.height}px` :
        document.body.style.paddingTop = 0;
    }

    componentWillUnmount(){
        this.mount = false;
    }

    render() {
        return (
            <div>
                <header className= {`header-area header-padding-1 section-padding-1 sticky-bar border-bottom header-3 ${this.state.scroll > this.state.top ? "stick" : ""}`}>
                    <div className="container-fluid">
                        <div className="row align-items-center">
                            <div className="col-xl-2 col-lg-2 col-md-6 col-4">
                                <div className="logo">
                                    <Link to={process.env.PUBLIC_URL+'/'}>
                                        <img src={process.env.PUBLIC_URL + this.props.logo} alt="" />
                                    </Link>
                                </div>
                            </div>
                            <div className="col-xl-8 col-lg-8 d-none d-lg-block">
                                <div className="offer-area text-center">
                                    <h5><span>Spring Summer</span> Sale off 50% <Link to={process.env.PUBLIC_URL + '/shop-sidebar'}>SHOPPING NOW</Link></h5>
                                </div>
                            </div>
                            <div className="col-xl-2 col-lg-2 col-md-6 col-8">
                                {/* navbar icon */}
                                <NavbarIconsTwo />
                            </div>
                        </div>
                    </div>
                </header>

                {/* header search */}
                <HeaderSearch />
                
                {/* header navigation menu aside */}
                <HeaderNavigationAside />
                
                {/* header cart aside */}
                <HeaderCartAside />

                {/* mobile menu */}
                <MobileMenu />

            </div>
        )
    }
}

export default HeaderTwo