import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import {animateScroll as scroll } from 'react-scroll'


class FooterThree extends Component{

    constructor(props){
        super(props);
        this.state = {};
        this.handleScroll = this.handleScroll.bind(this);
        this.scrollToTop = this.scrollToTop.bind(this);
    }

    handleScroll() {
        if(this.mount){
            this.setState({scroll: window.scrollY});
        }
    }

    scrollToTop (){
        scroll.scrollToTop();
    }
  
    componentDidMount() {
        this.mount = true;
        const el = document.querySelector('nav');
        this.setState({top: el.offsetTop, height: el.offsetHeight});
        window.addEventListener('scroll', this.handleScroll);
    }
    
    componentDidUpdate() {
        this.state.scroll > this.state.top ? 
        document.getElementById("scroll-top").classList.add("show") :
        document.getElementById("scroll-top").classList.remove("show") ;
    }

    componentWillUnmount(){
        this.mount = false;
    }

    render(){
        return (
            <div>
                <footer className="footer-area section-padding-1 pb-32 pt-45">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-xl-4 col-lg-5 col-md-4">
                                <div className="footer-widget copyright-2 mb-10">
                                    <p><Link to={process.env.PUBLIC_URL + '/'}>Gemshein</Link>. All Rights Reserved</p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-lg-2 col-md-4">
                                <div className="footer-widget footer-social-2 mb-10 text-center">
                                    <a href="//facebook.com"><i className="fa fa-facebook"></i></a>
                                    <a href="//twitter.com"><i className="fa fa-twitter"></i></a>
                                    <a href="//pinterest.com"><i className="fa fa-pinterest-p"></i></a>
                                </div>
                            </div>
                            <div className="col-xl-4 col-lg-5 col-md-4">
                                <div className="footer-widget footer-address mb-10">
                                    <p>PO Box 16122 Collins Street West Victoria 8007 Australia</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>

                {/* scroll top */}
                <button className="scroll-top" id="scroll-top" onClick={this.scrollToTop}>
                    <i className="fa fa-angle-double-up" />
                </button>
            </div>
        )
    }
}

export default FooterThree
