import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import HeaderNavMenu from './components/HeaderNavMenu'
import NavbarIconsThree from './components/NavbarIconsThree'
import HeaderSearch from './components/HeaderSearch'
import HeaderNavigationAside from './components/HeaderNavigationAside'
import HeaderCartAside from './components/HeaderCartAside'
import MobileMenu from './components/MobileMenu'

class HeaderThree extends Component {
    render() {
        return (
            <div>
                <div className="home-sidebar-left-2">
                    <div className="logo text-center">
                        <Link to="/">
                            <img alt='' src={process.env.PUBLIC_URL + this.props.logo} />
                        </Link>
                    </div>
                    <p>Beautiful eCommerce React Themplate</p>
                    
                    {/* navbar icons */}
                    <NavbarIconsThree />

                    <div className="sidebar-menu">
                        {/* header nav menu */}
                        <HeaderNavMenu />
                    </div>
                </div>
                
                {/* header search */}
                <HeaderSearch />
                
                {/* header navigation menu aside */}
                <HeaderNavigationAside />
                
                {/* header cart aside */}
                <HeaderCartAside />

                {/* mobile menu */}
                <MobileMenu />
            </div>
        )
    }
}

export default HeaderThree
